import React from 'react';
import { Card, CardBody, CardTitle, CardSubtitle, CardText, CardLink } from 'reactstrap';


class More extends React.Component {

    state = {
        city: ['new york', 'tel aviv', 'paris'],
        staticCities: []
    }
    componentDidMount() {
        this.getCrawler() //get data
    }

    render() {
        if (this.state.city === undefined) {
            return (
                <div>
                    <h1>Loading..</h1>
                </div>
            )
        } else {
            return (

                <div>
                    <h1>More Locations</h1>
                    <div class='moreCities' style={{ display: 'flex' }}>
                        {this.state.staticCities.map(c =>
                            <Card className='card firstRow' style={{ width: '10rem', margin: '10px', backgroundColor: "rgba(255, 255, 255, 0.2)" }}>
                                <CardBody>
                                    <CardTitle><h6>{c.City}</h6></CardTitle>
                                    <CardTitle>
                                        {c.Temp}
                                    </CardTitle>
                                    <CardText> <img className='img' src={c.Image}></img></CardText>
                           <h6 class="text-primary" >{c.Discription} </h6>
                                </CardBody>
                            </Card>
                        )}
                    </div>
                </div>
            );
        }
    }

    getCrawler() {
        for (let i = 0; i < this.state.city.length; i++) { // loop threw state city to get data for static cities
            var city = this.state.city[i];
            fetch(`http://localhost:3000/getlink/${city}`)
                .then(res => res.json())
                .then(results => {
                    var cityUni = results
                    this.getData(cityUni, this.state.city[i])

                })
        }
    }

    getData(cityUni, city) {
        return fetch(`http://localhost:3000/getforecast?cityUni=${cityUni}`)
            .then(res => res.json())
            .then(results => {
                results.City = city;
                this.state.staticCities.push(results);
            })
    }

}

export default More;