import React from 'react';
import './App.css';
import More from './comps/More';

import { Button, Input, Modal, Card, CardBody, Alert, NavbarBrand, Navbar, Nav, Container } from "reactstrap";

var Day = require('./day.jpg');
var Night = require('./night.jpg');

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      modalOpened: true,
      setmodalOpened: true,
      visible: false
    };
    this.onDismiss = this.onDismiss.bind(this);

  }
  state = {
    cityWeather: '',
    image: '',
    navbarColor: '',
    buttonColor: '',
    cityInfo: [],
  }

  onDismiss() { //close and open modal
    this.setState({ visible: !this.state.visible });
  }
  componentDidMount() {// get hour - change colors accordingly
    var now = new Date();
    var hour = now.getHours();
    if (hour > 4 && hour < 18) {
      document.body.style.backgroundImage = 'url(' + Day + ')';
      
      this.state.navbarColor = 'transparent';
      this.state.buttonColor = 'warning';

    } else {
      document.body.style.backgroundImage = 'url(' + Night + ')';
      this.state.navbarColor = "info"
      this.state.buttonColor = 'primary'
    }
    fetch(`http://www.geoplugin.net/json.gp`) // fetch current location by user location 
      .then(r => r.json())
      .then(data => {
        this.setState({ city: data.geoplugin_city })
        this.getCrawler();
      });
  }
  render() {
    return (
      <>
        <Navbar color={this.state.navbarColor} expand="md" style={{ fontSize: '20px', color: 'white' }}>
          <Container>
            <NavbarBrand style={{ fontSize: '20px' }} >
              Weather
          </NavbarBrand>
            <Nav style={{ display: 'inline' }} navbar>
              <Input onKeyPress={this.onKeyPress.bind(this)} style={{ height: '2rem', width: '10rem', margin: '10px', backgroundColor: "rgba(255, 255, 255, 0.2)", display: 'inline ' }} className='myCity' onChange={this.handleText.bind(this)} name='city' type='text' value={this.state.defaultCity} />
              <Button color={this.state.buttonColor}
                onClick={this.getCrawler.bind(this)}>Change Location</Button>
            </Nav>
          </Container>
        </Navbar>
        <Alert color="danger" isOpen={this.state.visible} toggle={this.onDismiss}>
          <b>Oh oh...</b> We are so SORRY! we don't have the trip tips for your location! the tips for now are in your area - Have fun!
</Alert>

        <Modal isOpen={this.state.modalOpened}>
          <div className="modal-header">
            <button
              aria-label="Close"
              className="close"
              data-dismiss="modal"
              type="button"
              onClick={() => this.setState({ modalOpened: false })}
            >
              <span aria-hidden={true}>Ã—</span>
            </button>
            <h5 className="modal-title" id="exampleModalLiveLabel">
              Welcome to Weather!         </h5>

          </div>
          <div className="modal-body">
            <p>Which location would you like your forecast for?</p>
            City:<Input onKeyPress={this.onKeyPress1.bind(this)} style={{ width: '10rem', margin: '10px', backgroundColor: "rgba(255, 255, 255, 0.2)", display: 'inline ' }} className='myCity' onChange={this.handleText.bind(this)} name='city' type='text' value={this.state.defaultCity} />
          </div>
          <div className="modal-footer">
            <div className="left-side">
              <Button
                className="btn-link"
                color="default"
                data-dismiss="modal"
                type="button"
                onClick={() => {
                  this.getCrawler();
                  this.setState({ modalOpened: false });
                }}
              >
                Get Weather!
            </Button>
            </div>
            <div className="divider" />
            <div className="right-side">
              <Button
                className="btn-link"
                color="success"
                type="button"

                onClick={() => this.setState({ modalOpened: false })}
              >
                Use My Location
            </Button>
            </div>
          </div>
        </Modal>

        <div className="App cardMainContainer">
          <div className='mainDiv'>
            <Card style={{ textAlign: 'left', backgroundColor: "rgba(255, 255, 255, 0.2)" }}>
              <CardBody className='containerMain'>
                <h2 className='today' id='title'></h2>
                <h3 className='today' id='weater' className='weather'></h3>
                <img id='image'></img>
                <h5 className='today' id='wind' className='weather'></h5>
                <h5 className='today' id='humidity' className='weather'></h5>
                <h5 id='descriptionWeather' style={{ color: 'black', margin: '0px' }} ></h5>
              </CardBody>
            </Card>

            <div class='moreCities' style={{display:'flex'}} >

              <More/>
            </div>
          </div>
        </div></>
    );
  }
  handleText(ev) {
    this.setState({ [ev.target.name]: ev.target.value })
  }


  onKeyPress = (e) => { // makes enter key board press
    if (e.which === 13) {
      this.getCrawler();
    }
  }

  onKeyPress1 = (e) => {// makes enter key board press
    if (e.which === 13) {
      this.getCrawler();
      this.setState({ modalOpened: false });

    }
  }

  getCrawler() { //get specific link by crawling.
    return fetch(`http://localhost:3000/getlink/${this.state.city}`)
      .then(res => res.json())
      .then(results => {
        var cityUni = results
        this.getData(cityUni)

      })
  }

  getData(cityUni) {// get by link from 'getcrawler' data from weather website
    return fetch(`http://localhost:3000/getforecast?cityUni=${cityUni}`)
      .then(res => res.json())
      .then(results => {
        this.state.cityWeather = results.Temp;
        console.log(results);
        this.setState({ allWeather: results })
        this.state.image = results.Image;
        document.getElementById('title').innerText = results.CityName;
        document.getElementById('wind').innerText = 'Wind ' + results.Wind;
        document.getElementById('humidity').innerText = results.Humidity;
        document.getElementById('image').src = results.Image;
        document.getElementById('weater').innerHTML = this.state.cityWeather;
        document.getElementById('descriptionWeather').innerHTML = results.Discription;

      })
  }


}


export default App;

